import React, { Component } from 'react';

const okI={
  "name" : "ok&&&",
  "lastName" : "LAST"
}
class Aside extends Component {
  constructor(props){super(props);
    this.state = {date: new Date()};
 }
 componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    this.setState({
      date: new Date()
    });  }
  render() {
    return (
      <React.Fragment>
        <h1></h1>

        <input type="text" id="inp" value="" name="okName" placeholder="What you the name?" />
        <h2> {this.state.date.toLocaleTimeString()}.</h2>
        <button onClick={console.log("Timer")} >Click</button>
        <h3>Amir SuperHero{

        }</h3>
      </React.Fragment>
    );
  }
}

export default Aside;



