import React, { Component } from 'react';
import './header.css';
import NewImage from '../nov.jpg';
import NewImage1 from '../nov1.jpg';
import NewImage2 from '../nov2.jpg';
import NewImage3 from '../nov3.jpg';
import NewImage4 from '../nov4.jpg';
import NewImage5 from '../nov5.jpg';
import NewImage7 from '../nov7.jpg';
import Screen from '../Screen.png';

// import React, { useState } from 'react';

const Person=()=><h1 style={{color:"red"}}>Amir</h1>;
const Imag=[NewImage,NewImage1,NewImage2,NewImage3,NewImage4,NewImage5,NewImage7];
let oj=+0;
class Header extends Component {
  constructor(props){
    super(props);
    this.state={
      count:+0
    };
  }
  render() {
    return (
      <React.Fragment>

      <h1></h1>
      <h2>Hi, <span id="name"></span></h2>
      <p>  {this.state.count} ()</p>
      <span>{console.log(this.state.count+' O_o')}</span>
      <img src={Imag[this.state.count]||Screen} alt="#" title="#" style={{width:"900px",maxWidth:"100%"}} onClick={()=>{if(this.state.count<Imag.length){
        document.querySelector('img').setAttribute('src', Imag[this.state.count]);
        console.log(this.state.count);
        this.setState({count:this.state.count+1});
      }else if(this.state.count==1){
          document.querySelector('img').setAttribute('src', Imag[this.state.count]);
          this.setState({count:this.state.count+1})
      }else if(this.state.count==2){
          document.querySelector('img').setAttribute('src', Imag[this.state.count]);
          this.setState({count:this.state.count+1})
      }else if(this.state.count==3){
          document.querySelector('img').setAttribute('src', Imag[this.state.count]);
          this.setState({count:this.state.count=+1});
      }else if(this.state.count==4){
          document.querySelector('img').setAttribute('src', Imag[this.state.count]);
          this.setState({count:this.state.count=+1});
      }else if(this.state.count==5){
          document.querySelector('img').setAttribute('src', Imag[this.state.count]);
          this.setState({count:this.state.count=+1});
      }else if(this.state.count==6){
          document.querySelector('img').setAttribute('src', Imag[this.state.count]);
          this.setState({count:this.state.count=+1});
      }else if(this.state.count==7){
          this.setState({count:this.state.count=0});
console.log("ooooooo!");
}else{console.log("ELSE")}
    }}/>
      </React.Fragment>
    );
  }
}

export default Header;



